html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 2;
}

h1,
h2,
h3,
h4 {
  margin: 2rem;
  line-height: 1.2;
  font-family: "Baloo 2", cursive;
}

h1 {
  font-size: 7rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

a,
p {
  font-family: "Montserrat", sans-serif;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
  line-height: 1.6;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  width: 100%;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.ocultar {
  display: none;
}
@media (min-width: 700px) {
  .ocultar {
    display: inline;
  }
}

.btn {
  padding: 1rem 3rem;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  text-align: center;
  border: none;
  color: #fefefcfb;
}
.btn:hover {
  cursor: pointer;
}

.azul {
  background: #006fe6;
  transition: all 0.2s ease-in-out;
}
.azul:hover {
  background: #fefefcfb;
  border: 2px solid #006fe6;
  color: #006fe6;
}

.edge {
  border: 2px solid #006fe6;
  color: #006fe6;
  display: block;
  text-align: center;
  transition: 0.2s all ease-in-out;
}
@media (min-width: 700px) {
  .edge {
    display: inline-block;
    margin-top: 2rem;
  }
}
.edge:hover {
  color: #fefefcfb;
  background: #006fe6;
}

@keyframes desplazar {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
.header {
  position: relative;
  height: 100vh;
  min-height: 70rem;
  background: url(../assets/img/pexels-christina-morillo-1181396.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  z-index: 1;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
@media (min-width: 700px) {
  .header {
    background-attachment: fixed;
  }
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  top: 0;
  width: 100%;
  height: 7rem;
  background: #fefefcfb;
  box-shadow: 2px 1rem 1.6rem -6px rgba(0, 0, 0, 0.45);
  z-index: 1;
}
@media (min-width: 700px) {
  .header__nav .hamburguer {
    display: none;
  }
}
.header__nav .hamburguer span {
  display: block;
  width: 4rem;
  height: 1rem;
  border: 3px solid white;
  background-color: #07070a;
}

.logo h2 span {
  color: #006fe6;
}

.nav {
  position: absolute;
  width: 100%;
  top: 6rem;
  text-align: center;
  background: #fefefcfb;
  animation: desplazar 0.5s ease-in-out;
}
.nav a {
  display: block;
  font-size: 2rem;
  padding: 1.5rem;
  color: #07070a;
  transition: 0.2s all ease-in-out;
}
@media (min-width: 700px) {
  .nav a {
    display: inline;
  }
}
.nav a:hover {
  background-color: #006fe6;
  color: #fefefcfb;
}
@media (min-width: 700px) {
  .nav {
    position: relative;
    top: unset;
    width: auto;
    background: unset;
    animation: unset;
  }
}

/* CONTENIDO-HEADER */
@media (min-width: 700px) {
  .header__content,
.header__form {
    flex: 0 0 calc(50% - 1rem);
  }
}

@media (min-width: 700px) {
  .header__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 85%;
  }
}
.header__content .header__text {
  padding-top: 8rem;
  text-align: center;
  color: #fefefcfb;
  margin-bottom: 1rem;
}
@media (min-width: 700px) {
  .header__content .header__text {
    padding-top: 0;
  }
}
.header__content .header__text h1 {
  font-size: 4rem;
}
@media (min-width: 700px) {
  .header__content .header__text h1 {
    text-align: left;
  }
}

.header__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fefefcfb;
  padding: 5rem;
  border-radius: 0.5rem;
}
.header__form legend h2 {
  margin-top: 0;
  text-align: center;
}
.header__form .header__input {
  margin-bottom: 1rem;
  width: 100%;
}
.header__form .header__input input {
  width: 100%;
  padding: 0 1.5rem;
  height: 4.5rem;
  border: 1px solid #f1f1f1;
  border-radius: 0.5rem;
  font-family: "Montserrat", sans-serif;
  outline: none;
}

.advantages {
  background: #0a1128;
  padding: 5rem;
}
@media (min-width: 700px) {
  .advantages__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
.advantages__advantage {
  color: #fefefcfb;
  text-align: center;
}
.advantages__advantage i {
  font-size: 4rem;
  text-align: center;
}

.about h2 {
  text-align: center;
  color: #006fe6;
}
.about__content {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  background: url(../assets/img/pexels-tranmautritam-326511.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 700px) {
  .about__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    background-attachment: fixed;
  }
}
.about__text {
  grid-row: 2/3;
  background: #fefefcfb;
  padding: 2rem;
}
@media (min-width: 700px) {
  .about__text {
    padding: 4rem;
  }
}
.about__text h3 {
  text-align: center;
  color: #006fe6;
}
@media (min-width: 700px) {
  .about__text h3 {
    text-align: left;
  }
}
.about__text p {
  text-align: left;
}

.services {
  padding-top: 5rem;
}
.services h2 {
  text-align: center;
  color: #006fe6;
}
.services__content {
  display: grid;
  text-align: left;
}
@media (min-width: 700px) {
  .services__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
.services__service {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.services__service i {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #006fe6;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  font-size: 3rem;
  color: #fefefcfb;
  box-shadow: 1rem 1rem 1.6rem -6px rgba(0, 0, 0, 0.45);
  transition: all 0.2s ease-in-out;
}
.services__service h3 {
  font-size: 2.4rem;
  color: #0a1128;
}

.contact {
  padding-top: 5rem;
}
.contact h2 {
  text-align: center;
  color: #006fe6;
}
.contact__content {
  display: grid;
}
@media (min-width: 700px) {
  .contact__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0rem;
  }
}
.contact__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
  position: relative;
  background: url(../assets/img/pexels-thisisengineering-3861967.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  text-align: center;
  z-index: -1;
}
@media (min-width: 700px) {
  .contact__info {
    height: 100vh;
  }
}
.contact__info::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.contact__info h3,
.contact__info p {
  color: #fefefcfb;
}
.contact__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5rem;
  background: #0a1128;
}
.contact__input label {
  color: #fefefcfb;
  font-family: "Montserrat", sans-serif;
  display: block;
  margin: 1rem 0;
  font-weight: bold;
}
.contact__input input {
  font-family: "Montserrat", sans-serif;
  padding: 1.2rem 2rem;
}
.contact__input input [type=submit] {
  width: auto;
  padding: 1rem 3rem;
}
.contact__input textarea {
  resize: none;
}
.contact__input input,
.contact__input textarea {
  width: 100%;
  outline: none;
}

@media (min-width: 700px) {
  .footer {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}
.footer .logo h2 {
  text-align: center;
}
.footer p {
  text-align: center;
}